import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

export interface Req {
  'hub.mode': string,
  'hub.topic': string,
  'hub.callback': string,
  'hub.lease_seconds': string
}

export interface ReqStream {
    'hub.mode': string,
    'hub.topic': string,
    'hub.callback': string,
    'hub.lease_seconds': string,
    // 'hub.challenge': string
    // 'hub.secret': string,
    // 'scopes': Array<any>
}

@Injectable({
    providedIn: 'root'
})

export class TwitchService {

    streamer = 'juice'

    constructor(private http: HttpClient) {}

    getAuth(): Observable<any> {
        // e.preventDefault();
        const headers = new HttpHeaders({
            'Client-id': '4teskombu99lkpk9ajepfjwn1ovzm9',
            Authorization: 'Bearer 8jvm9tkhqprdnp2rfdnrr02qx0nvs5',
            redirect_uri: 'http://localhost',
            response_type: 'code',
            // scope: 'viewing_activity_read',
            // state: ''
        })
        // const params = new HttpParams();
        // params.append('response_type', 'code');
        // params.append('client_id', '4teskombu99lkpk9ajepfjwn1ovzm9');
        // params.append('redirect_params', 'http://localhost');
        // params.append('redirect_uri', 'http://localhost');
        // params.append('scope', 'viewing_activity_read');
        return this.http.get<any>(`https://id.twitch.tv/oauth2/authorize?response_type=code&client_id=4teskombu99lkpk9ajepfjwn1ovzm9&redirect_uri=http://localhost`, {
            headers,
            // params,
        });
    }

    fetchTwitch(): Observable<any> {
        // const headers = new HttpHeaders({
            // 'Client-ID': 'kimne78kx3ncx6brgo4mv6wki5h1ko',
            // 'Authorization': 'Bearer 8jvm9tkhqprdnp2rfdnrr02qx0nvs5',
            // 'OAuth': 'Bearer 8jvm9tkhqprdnp2rfdnrr02qx0nvs5'
        // })
        // params = params.append('')
        return this.http.get<any>('https://api.twitch.tv/helix/streams?user_login=' + this.streamer, {
            headers: new HttpHeaders({
                'client-id': 'kimne78kx3ncx6brgo4mv6wki5h1ko',
                Authorization: 'Bearer 8jvm9tkhqprdnp2rfdnrr02qx0nvs5'
            })
        })
    }

    followHookPost(req: Req): Observable<Req> {
        return this.http.post<any>('https://api.twitch.tv/helix/webhooks/hub', req,
        {
            headers: new HttpHeaders({
                'client-id': 'kimne78kx3ncx6brgo4mv6wki5h1ko',
                Authorization: 'Bearer 8jvm9tkhqprdnp2rfdnrr02qx0nvs5'
            })
        })
    }

    streamHookPost(req: ReqStream): Observable<ReqStream[]> {
        return this.http.post<any>('https://api.twitch.tv/helix/webhooks/hub', req,
        {
            headers: new HttpHeaders({
                'client-id': 'kimne78kx3ncx6brgo4mv6wki5h1ko',
                Authorization: 'Bearer 8jvm9tkhqprdnp2rfdnrr02qx0nvs5',
                'Access-Control-Allow-Headers': '*',
            })
        })
    }


}